import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { observer } from 'mobx-react-lite'
import { useStores } from '../../models/root-store/root-store-context'
import './AdminPedidos.css'
import { Page, SDataTable } from '../../components'
import { RouteComponentProps } from 'react-router'
import Select from 'react-select'
import {
  PEDIDO_ESTADOS_RUTEO_SELECT,
  PEDIDO_ESTADOS_RUTEO_STR,
  PEDIDO_ESTADOS_SELECT,
  PEDIDO_ESTADOS_STR,
  RUTAS,
} from '../../constants'
import { fecha_fmt, plata_fmt } from '../../helpers'
import { protectedRef } from '../../helpers-ts'

export const AdminPedidos: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const DataTable = React.createRef<any>()
  const [Estado, setEstado] = useState<any>(null)
  const [EstadoRuteo, setEstadoRuteo] = useState<any>(null)
  const [MetodoEntrega, setMetodoEntrega] = useState<any>(null)

  const visualizar = (row: any) => {
    history.push(RUTAS.ADMIN_PEDIDO_DETAIL(row.id))
  }

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}>
          <FIcon.Eye size={14} />
        </span>
      </div>
    )
  }

  const estados_all_filter = function <T>(original: T): { label: string; value: number }[] {
    // @ts-ignore
    return [{ value: -1, label: 'Todos' }].concat(original)
  }

  useEffect(() => {
    DataTable && DataTable.current.refresh()
    DataTable && DataTable.current.clearSelected()
  }, [Estado, EstadoRuteo, MetodoEntrega])

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Pedidos</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Pedidos</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <div className={'row'}>
                <div className={'col-4'}>
                  <h4 className={'mb-4'}>Estado</h4>
                  <Select
                    defaultValue={estados_all_filter([])[0]}
                    onChange={(val) => {
                      setEstado(val ? val.value : -1)
                    }}
                    placeholder={'Elegir estado'}
                    options={estados_all_filter(PEDIDO_ESTADOS_SELECT)}
                    theme={(theme) => ({
                      ...theme,
                      colors: {
                        ...theme.colors,
                        primary: '#303AB2',
                        neutral50: 'black',
                      },
                    })}
                  />
                </div>
                <div className={'col-4'}>
                  <h4 className={'mb-4'}>Estado ruteo</h4>
                  <Select
                    defaultValue={estados_all_filter([])[0]}
                    onChange={(val) => {
                      setEstadoRuteo(val ? val.value : -1)
                    }}
                    placeholder={'Elegir estado'}
                    options={estados_all_filter(PEDIDO_ESTADOS_RUTEO_SELECT)}
                    theme={(theme) => ({
                      ...theme,
                      colors: {
                        ...theme.colors,
                        primary: '#303AB2',
                        neutral50: 'black',
                      },
                    })}
                  />
                </div>
              </div>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={protectedRef(DataTable)}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  return rootStore.environment.api.adminPedidos(page, perPage, order, search, {
                    estado: Estado == -1 ? null : Estado,
                    estado_ruteo: EstadoRuteo == -1 ? null : EstadoRuteo,
                  })
                }}
                columns={[
                  {
                    name: 'Referencia',
                    selector: 'referencia',
                    sortable: true,
                  },
                  {
                    name: 'Fecha',
                    selector: 'fecha',
                    sortable: false,
                    cell: (row: any) => fecha_fmt(row.fecha, 'DD/MM/YYYY'),
                  },
                  {
                    name: 'Total',
                    selector: 'total',
                    sortable: true,
                    cell: (row: any) => plata_fmt(row.total),
                  },
                  {
                    name: 'Precio mejorado',
                    selector: 'precio_mejorado',
                    sortable: true,
                    cell: (row: any) => (row.precio_mejorado ? 'Si' : 'No'),
                  },
                  {
                    name: 'Cliente',
                    selector: 'usuario',
                    sortable: true,
                    cell: (row: any) => (
                      <a onClick={() => history.push(RUTAS.ADMIN_CLIENTE_DETAIL(row.usuario.id))}>{row.usuario.id} </a>
                    ),
                  },
                  {
                    name: 'Estado',
                    selector: 'estado',
                    sortable: false,
                    cell: (row: any) => {
                      return PEDIDO_ESTADOS_STR[row.estado]
                    },
                  },
                  {
                    name: 'Estado ruteo',
                    selector: 'ruteo',
                    sortable: false,
                    cell: (row: any) => {
                      return row.estado_ruteo ? PEDIDO_ESTADOS_RUTEO_STR[row.estado_ruteo] : ''
                    },
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminPedidos
